.Nbtn {
  width: 75px;
  height: 75px;
  font-size: 40px;
  background-color: white;
  border: 2px black solid;
  color: black;
  margin: 2px;
  border-radius: 10px;
}

.Sbtn {
  width: 75px;
  height: 75px;
  font-size: 40px;
  background-color: Black;
  border: 2px white solid;
  color: white;
  margin: 2px;
  border-radius: 10px;
}

.button-wrap {
  margin-top: 150px;

  padding: 10px;
  width: 500;
  text-align: center;
}

.Nbtn:hover {
  text-shadow: 0px 0px 6px rgba(255, 255, 255, 1);

  transition: all 0.4s ease 0s;
}

.Sbtn:hover {
  text-shadow: 0px 0px 6px rgba(255, 255, 255, 1);

  transition: all 0.4s ease 0s;
}
